import React from 'react';
import { UserContext, Card } from './context';

const ATMWithdraw = ({ onChange, isValid }) => {
    
    return (
        <>
            <div>
                Withdraw amount:
            </div>
            <div>
                <input type="text" onChange={onChange}></input>
            </div>
            <br />
            <div>
                <input type="submit" disabled={!isValid} value="Withdraw"></input>
            </div>
        </>
    );
};

const Withdraw = () => {
    const ctx = React.useContext(UserContext);
    let currentUserN = (ctx.users.length) - 1;
    let initialUserBalance = ctx.users[currentUserN].balance;

    const [transactionValue, setTransactionValue] = React.useState(0);
    const [validTransaction, setValidTransaction] = React.useState(false);
    const [currentBalance, setCurrentBalance] = React.useState(initialUserBalance);

    let currentUserGreeting = "Hello " + ctx.users[currentUserN].name;

    let status = `Balance $${currentBalance}`;

    const handleChange = event => {
        if (isNaN(event.target.value) === true ) {
            return (
                alert('Enter Numerical values only'),
                setValidTransaction(false)
            )
        } else if 
        (Number(event.target.value) === 0) {
            return setValidTransaction(false);
        } else {
            setValidTransaction(true);
        }
        setTransactionValue(Number(event.target.value));
    };

    const handleSubmit = (event) => {
        if (transactionValue >= currentBalance) {
            return (
                setValidTransaction(false),
                alert('Transaction Failed'),
                event.preventDefault(),
                setValidTransaction(true)
                )
            } else {
                let updatedUserBalance = currentBalance - transactionValue;
                ctx.users[currentUserN].balance = updatedUserBalance;
                setCurrentBalance(updatedUserBalance);
                status = `Balance $${currentBalance}`;
                setValidTransaction(false);
                alert('Success!');
                event.preventDefault();
            }
    };


    if (ctx.users.length > 1) {
        return (
            <Card
            txtcolor="black"
            header="Withdraw"
            title={currentUserGreeting}
            body={
                <form onSubmit={handleSubmit}>
                    <br />
                    <h4 id="total">{status}</h4>
                    <br />
                    <ATMWithdraw onChange={handleChange} isValid={validTransaction}></ATMWithdraw>
                    </form>
                }
            />
        )
    }
    else {
        return (
            <>
                <p>Please create an Account first</p>
            </>
        )
    }
}

export default Withdraw;
