import React from 'react';
import { UserContext,
        Card } from './context';

function AllData() {
    const ctx = React.useContext(UserContext);
        
        const loggedInUsers = [...ctx.users];
        const userTest = loggedInUsers.shift();

        function printOut(user) {
            return (
                <>
                    <div>
                        <hr />
                        <b>Name:</b> {user.name}
                        <br/>
                        <b>Email:</b> {user.email}
                        <br/>
                        <b>Password:</b> {user.password}
                    </div>
                </>
            )
        }

        loggedInUsers.map(printOut);


    return (
        <>
            <Card
                txtcolor="black"
                header="All Data"
                title="Logged In Accounts"
                body={
                    <>
                        <div class="card-table table">
                            {loggedInUsers.map(printOut)}
                        </div>
                    </>

                }
            />
        </>
    );
}

export default AllData;
