import React from 'react';
import {UserContext, Card} from './context';



function CreateAccount() {
    const [show, setShow] = React.useState(true);
    const [status, setStatus] = React.useState('');
    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [password, setPassword] = React.useState('');
    const [showAccountButton, setShowAccountButton] = React.useState(false);

    const ctx = React.useContext(UserContext);

    const handleChange = event => {
        if (name.length > 0 || email.length > 0 || password.length > 0){
        setShowAccountButton(true);
        }
    };

    function validate(field, label, event) {
        if (!field) {
            setStatus(
                <div className="pt-4"> Please enter {label} first </div>
                );
            setTimeout(() => setStatus(''), 5000);
            event.preventDefault()
            return false;
        }
        return true;
    }

    function validatePassword(field, label, event) {
        if (password.length < 8) {
            setStatus(
                <div className="pt-4"> The {label} password has to be at least 8 characters long</div>
                );
            setTimeout(() => setStatus(''), 5000);
            console.log(status);
            event.preventDefault()
            return false;
        }
        return true;
    }

    function handleCreate(event) {
        console.log(name, email, password);
        if (!validate(name, "name")) return; // if this pass
        if (!validate(email, "email")) return; // if this pass
        if (!validatePassword(password, "password")) return; // if this pass
        alert("success!");
        ctx.users.push({name, email, password,balance:0});
        setShow(false);
    }

    function clearForm() {
        setName('');
        setEmail('');
        setPassword('');
        setShow(true);
    }

    return (
        <Card
            bgcolor="primary"
            header="Create Account"
            status={status}
            body={show ? (
                <form onChange={handleChange}>
                    Name <br/>
                    <input type="input" className="form-control" id="name" placeholder="Enter Name" value={name} onChange={e => setName(e.currentTarget.value)}/><br/>
                    Email
                    <input type="input" className="form-control" id="email" placeholder="Enter Email" value={email} onChange={e => setEmail(e.currentTarget.value)}/><br/>
                    Password
                    <input type="password" className="form-control" id="password" placeholder="Enter Password" value={password} onChange={e => setPassword(e.currentTarget.value)}/><br/>
                    <button type="submit" className="btn btn-light" disabled={!showAccountButton} onClick={handleCreate}>Create Account</button>
                    <br/>
                </form>
                ) : (
                <>
                <h5>Success</h5>

                <button type="submit" className="btn btn-light" onClick={clearForm}>Add Another Account</button>
                </>
                )
            }        
        />
    );
}

export default CreateAccount;